import config from "./config";
import axios from 'axios';
import NProgress from 'nprogress' // progress bar

axios.interceptors.request.use(config => {
    NProgress.start();
    return config;
});
axios.interceptors.response.use(config => {
    NProgress.done();
    return config;
}, error => {
    NProgress.done();
    return Promise.reject(error.response);
});

export default {
    getUsers(ids) {
        var requests = ids.map(id => this.requestBpmn(
            "identity/users/" + id
        ));
        return Promise.all(requests);
    },
    getItem(type, id) {
        var result;
        var url = config[type + "Url"];
        return axios.get(url + id).then(response => {
            result = response;
            var item = result.data;
            var ids = [];
            if (item.createdBy && ids.indexOf(item.createdBy) < 0) {
                ids.push(item.createdBy);
            }
            if (item.updatedBy && ids.indexOf(item.updatedBy) < 0) {
                ids.push(item.updatedBy);
            }
            return this.getUsers(ids);
        }).then(response => {
            var item = result.data;
            var data = response.map(item => item.data);
            if (item.createdBy) {
                var user = data.find(user => user.id.toLowerCase() === item.createdBy.toLowerCase());
                item.createdName = (user.firstName + " " + user.lastName).trim();
            }
            if (item.updatedBy) {
                user = data.find(user => user.id.toLowerCase() === item.updatedBy.toLowerCase());
                item.updatedName = (user.firstName + " " + user.lastName).trim();
            }

            return Promise.resolve(result);
        });
    },
    getItems(type, data) {
        var url = config[type + "Url"];
        return new Promise((resolve, reject) => {
            data = data || {};
            data.filter = data.filter || {};
            data.filter.type = type;
            data.filter = JSON.stringify(data.filter);

            if (data.sort) {
                data.sort = JSON.stringify(data.sort);
            }

            var settings = {
                dataType: "json",
                url: url,
                method: "get",
                params: data
            };
            var items = [];
            return axios(settings).then(response => {
                items = response.data._embedded;
                var ids = [];
                items.forEach(item => {
                    if (item.createdBy && ids.indexOf(item.createdBy) < 0) {
                        ids.push(item.createdBy);
                    }
                    if (item.updatedBy && ids.indexOf(item.updatedBy) < 0) {
                        ids.push(item.updatedBy);
                    }
                });
                return this.getUsers(ids);
                //   resolve(items);
            }).then(response => {
                items.forEach(item => {
                    var data = response.map(item => item.data);
                    if (item.createdBy) {
                        var user = data.find(user => user.id.toLowerCase() === item.createdBy.toLowerCase());
                        item.createdName = (user.firstName + " " + user.lastName).trim();
                    }
                    if (item.updatedBy) {
                        user = data.find(user => user.id.toLowerCase() === item.updatedBy.toLowerCase());
                        item.updatedName = (user.firstName + " " + user.lastName).trim();
                    }

                });
                resolve(items);
            });
        });
    },

    saveItem(type, item) {
      return  this.getItems(type, {
            filter: {
                name: item.name,
                _id: {
                    "$ne": item._id
                }
            }
        }).then(response => {
            if (response && response.length) {
                return Promise.reject({
                    message: "名称\"" + item.name + "\"已存在。"
                });
            } else {
                return axios({
                    dataType: "json",
                    url: config[type + "Url"],
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "post",
                    data: item
                });
            }

        });
    },
    deleteItem(type, id) {
        var url = config[type + "Url"];
        url += id;
        var settings = {
            dataType: "text",
            url: url,
            method: "delete",
            data: {}
        };

        return axios(settings);
    },
    requestBpmn(path, type, data) {
        var type = type || "GET";
        var url = config.bpmnUrl + (path || "");
        var settings = {
            dataType: "json",
            url: url,
            headers: {
                "Authorization": sessionStorage.getItem("token"),
                "Content-Type": "application/json"
            },
            method: type

        };
        if (type === "GET") {
            settings.params = data;
        } else {
            settings.data = data;
        }

        return axios(settings);
    },
    upload(formData) {
        return axios({
            method: "post",
            url: config.uploadUrl + "upload",
            data: formData,
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });
    },
    getProcessTemplate(type) {
        return axios({
            method: "get",
            url: "template/" + type + ".bpmn",
            headers: {
                'Content-Type': 'text'
            }
        });
    }
}
