<template>
  <div>
    <div class="blankpage-form-field">
      <div class="card p-4">
        <p align="center" class="mb-4">
          <img src="img/eorionlogoonly.svg" height="32px" />
        </p>
        <form novalidate="novalidate" onsubmit="return false;" autocomplete="false">
          <div class="form-group">
            <label class="form-label" for="name">用户名</label>
            <input
              type="text"
              id="name"
              class="form-control"
              required
              placeholder="用户名"
              v-model="user.name"
              autocomplete="false"
              :class="{'is-invalid': !validation.name}"
            />
          </div>
          <div class="form-group">
            <label class="form-label" for="password">密码</label>
            <input
              type="password"
              id="password"
              required
              class="form-control"
              placeholder="密码"
              v-model="user.password"
              autocomplete="false"
              :class="{'is-invalid': !validation.password}"
            />
          </div>

          <button v-on:click="login()" class="btn btn-default float-right">
            <i class="fal fa-fw fa-sign-in text-primary mr-2"></i>登录
          </button>
        </form>
      </div>
    </div>

    <div
      class="modal fade"
      id="errorModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="errorModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="errorModalTitle">登陆失败</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">登陆失败，用户名或密码错误。</div>
          <div class="modal-footer">
            <button type="button" class="btn btn-error" data-dismiss="modal">
              <i class="fal fa-fw fa-check mr-2"></i>确定
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "@/common/config";
// import utils from "@/common/utils.js";
import service from "@/common/service";
import auth from "@/common/auth";
import moment, { lang } from "moment";
export default {
  data() {
    return {
      moment: moment,
      config: config,
      user: {
        name: "",
        password: ""
      },
      validation: {
        name: true,
        password: true
      }
    };
  },
  created: function() {},
  mounted() {},
  methods: {
    login() {
      var allValid = true;
      for (var i in this.user) {
        var valid = !!(this.user[i] || "").trim();
        this.validation[i] = valid;
        if (!valid) {
          allValid.false;
        }
      }
      if (!allValid) {
        return;
      }
      auth.login(this.user.name, this.user.password).then(
        user => {
          if (this.$route.query.redirect && this.$route.query.redirect.indexOf('processCreate') != -1){
            this.$router.push({ path: this.$route.query.redirect + '&businessKey=' + this.$route.query.businessKey });
          }else {
            this.$router.push({ name: "home" });
          }
        },
        error => {
          $("#errorModal").modal("show");
        }
      );
    }
  }
};
</script>

